.news-catalog {

  $block-name: &; // #{$block-name}__element {}

  &__block {
    margin-bottom: 20px;

    @include media-breakpoint-up(md) {
      margin-bottom: 30px;
    }
  }

  &__page {
    display: flex;
    justify-content: center;
    margin-top: 90px;
  }
}
